
.hotStoreCharts,
.hotIndustry,
.trend {
  width: 100%;
  height: 10.666667rem;
}
.weui-search-bar__cancel-btn {
  color: #2da1df !important;
}
.HotStorecontent {
  padding: 0.266667rem 0 0 0;
  margin-bottom: 0.533333rem;
  background-image: -webkit-linear-gradient(#5cadff, #ffffff);
  background-image: linear-gradient(#5cadff, #ffffff);
  border-radius: 0.133333rem;
}
.otherChartsEntry {
  padding: 0.266667rem 0 0 0;
  border-radius: 0.133333rem;
  margin-bottom: 0.533333rem;
  background-image: -webkit-linear-gradient(#f87d7e, #ffffff);
  background-image: linear-gradient(#f87d7e, #ffffff);
}
.changesCharts {
  padding: 0.266667rem 0 0 0;
  border-radius: 0.133333rem;
  margin-bottom: 0.533333rem;
  background-image: -webkit-linear-gradient(#fcca57, #ffffff);
  background-image: linear-gradient(#fcca57, #ffffff);
}
.box1 {
  height: 1.333333rem;
  position: relative;
  width: 19.866667rem;
}
.box1-item {
  width: 50%;
  height: 1.333333rem;
  background-color: #ccc;
  display: inline-block;
  margin-left: 0.2rem;
  float: left;
  text-align: center;
  line-height: 1.333333rem;
}
.box1-item:first-child {
  margin-left: 0;
}
.weui-media-box__title {
  font-size: 0.213333rem !important;
}


