
.weui-cells:first-child {
  margin-top: 0 !important;
}
.list ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.list ul li {
  width: 90%;
  background: #fff;
  margin: 0 auto 0.266667rem;
  border-radius: 0.133333rem;
  padding: 0.2rem;
  box-shadow: #ccc 0.026667rem 0.026667rem 0.026667rem;
}
.list ul li .header {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  height: 1.333333rem;
  padding-top: 0.266667rem;
  margin-bottom: 0.266667rem;
}
.list ul li .header .name {
  -webkit-box-flex: 2;
  -webkit-flex: 2;
          flex: 2;
  color: #000;
  font-size: 0.4rem;
}
.list ul li .header .detail {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  color: #ccc;
  font-size: 0.266667rem;
  margin-bottom: 0.266667rem;
  cursor: pointer;
  text-align: right;
}
.list ul li .header .detail:hover {
  text-decoration: underline;
}
.list ul li .content {
  margin-bottom: 0.133333rem;
  color: #ccc;
  overflow: hidden;
  font-size: 0.4rem;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.list ul li .deal {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 0.133333rem;
}
.list ul li .deal .current {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.list ul li .deal .current span {
  color: #e53333;
}
.list ul li .deal .type {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.list ul li .deal .type span {
  color: #ccc;
}
.list ul li .begin {
  margin-bottom: 0.133333rem;
}
.list ul li .begin span {
  color: #ccc;
}
.list ul li .end {
  margin-bottom: 0.133333rem;
}
.list ul li .end span {
  color: #ccc;
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon > i,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: #1296db!important;
}


